.App {
  -ms-flex-align: center;
      align-items: center;
  min-height: 100vh;
  text-align: center;
  position: relative;
}

.App-border {
  border-top: 10px solid #50e3c2;
  margin: 0;
  width: 100%;
}

.App-home {
  top: 0;
  position: absolute;
  bottom: 0;
  right: 0;
  left: 0;
  padding-bottom: 40px;
}

.App-landing {
  margin-top: 240px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
}

.App-results {
  margin-top: 120px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
}

.App-export-group {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

.App-export-item {
  margin-right: 1.5em;
}
.App-export-item:last-child {
  margin-right: 0;
}

.App-export-item button {
  background: none;
  border: none;
  color: #4a4a4a;
}

.App-export-item button:disabled {
  opacity: 0.3;
}

.App-button {
  background: none;
  border: 1px solid #4a4a4a;
  border-radius: 999px;
  color: #4a4a4a;
  padding: 8px 2em;
  font-family: "Noto Serif", serif;
  font-size: 1.25em;
}

.App-button:hover {
  background: #4a4a4a;
  color: #ffffff;
  -webkit-transition: all 0.2s ease-out;
  -o-transition: all 0.2s ease-out;
  transition: all 0.2s ease-out;
}

.App-title {
  font-size: 2em;
  font-weight: normal;
  margin: 0;
}

.App-subtitle {
  font-size: 1.25em;
  font-weight: normal;
  margin: 0;
}

.App-textarea {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  height: 100%;
  margin-left: auto;
  margin-right: auto;
  max-width: 650px;
  padding: 120px 24px 40px 24px;
}

.App-textarea > textarea {
  color: #4a4a4a;
  font-size: 1.25em;
  font-family: "Noto Serif", serif;
  height: 100%;
  width: 100%;

  border: none;
  outline: none;
  resize: none;

  -webkit-box-shadow: none;
  box-shadow: none;
}

.App-textarea::-webkit-input-placeholder {
  color: #9b9b9b;
}

.App-footer {
  bottom: 0;
  font-size: 0.9em;
  left: 0;
  margin-bottom: 2em;
  position: absolute;
  right: 0;
}



